
.section_container03{

    clear: both;
    position: relative;
    width: 100%;
    height: 1300px;

    background: url("https://deadbydaylight.com/static/a5cdc5db3804f817df112184c81f5ead/663bd/dbd-gallery-background.webp");
    background-size: cover;
        background-attachment: fixed;
    }

.section03_wrapper{
    position: relative;
    width: 100%;
    height:  100%;
    background: rgba(10, 10, 10, 0.5);




    
}

.carousel_aria_A{
    position: absolute;
    width: 65%;
    top: 150px;
    left: 80px;
    bottom: 150px;
    background: rgba(10, 10, 10, 0.5);
}

.carousel_main{
    position: absolute;
    width: 850px;
    height: 550px;
    top: 250px;
    right:  150px;
    background: rgba(10, 10, 10, 0.5);
}





.main_border_line_01{
    position: absolute;
    top: -5px;
    left:-5px;

    width: 50px;
    height: 50px;
    background: red;
    z-index: 0;
}

.main_border_line_02{
    position: absolute;

    bottom: -5px;
    right:-5px;
    width: 50px;
    height: 50px;
    background: red;
    z-index: 0;

}

.carousel_main img{
    width: 100%;
    height:  100%;
    background-size: cover;
    position: absolute;
    z-index: 1;

}



.carousel_aria_B{
    position: absolute;
    width: 25%;
    top: 150px;
    right: 80px;
    bottom: 150px;
    background: rgba(10, 10, 10, 0.5);
}

.Carousel_Slider_wrraper{
    position: absolute;
    top: 10%;
    width: 100%;
    height:75%;
    overflow: hidden;
// background: blue;
bottom: 10%;
}

