.carousel_slider{
    display: flex;
    flex-flow: row wrap;
    position: absolute;
    width: 180px;
    height: 150px;
    top: 300px;
    right:  150px;
    background: rgba(10, 10, 10, 0.5);
    z-index: 0;

}

.sub_border_line_01{
    left:  -3px;
    top: -3px;
    bottom:  -3px;
    width: 30px;
    height: 156px;
    background: red;
    position: absolute;
    z-index: -1;
    
}

.sub_border_line_02{
    right: -3px;
    top: -3px;
    bottom:  -3px;
    width: 30px;
    height: 156px;
    background: red;
    position: absolute;
    z-index: -1;

}


